.button{
	font-style: 1rem;
	font-family: var(--type-first);
	cursor: pointer;
	border: none;
	border-radius: .4rem;
	background: #fb1;
	color: #764701;
	min-width: 8rem;
	padding: .8rem 1.2rem;
	box-sizing: border-box;
	transition: .1s;
}

.button:hover,
.button:focus{
	outline: none;
	box-shadow: 0 0 0 3px #fea, 0 0 0 4px #fb1;
}

.button:disabled{
	opacity: .5;
	cursor: wait;
}